import { createTheme } from "@mui/material";
import { useMemo } from "react";
import { CssBaseline } from "@mui/material";
import { GlobalStyles } from "@mui/material";

export const useMuiTheme = () => {
  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          primary: {
            main: "#f97020", //orange
            black: "#050403",
            white: "#f7f7f7",
          },
        },
        typography: {
          fontFamily: "Roboto, Arial, sans-serif", // Default font family
          fontWeightRegular: 400,
          fontWeightMedium: 500,
          fontWeightBold: 700,
          h1: {
            fontFamily: "Montserrat, Arial, sans-serif",
            fontWeight: 700,
            fontSize: "3rem",
          },
          h2: {
            fontFamily: "Montserrat, Arial, sans-serif",
            fontWeight: 600,
            fontSize: "2.5rem",
          },
          h5: {
            fontFamily: "Montserrat, Arial, sans-serif",
            fontWeight: "bold",
            fontSize: "1rem",
            textTransform: "uppercase",
            color: "#f7f7f7",
          },
          h6: {
            fontFamily: "Montserrat, Arial, sans-serif",
            fontWeight: "bold",
            fontSize: "1rem",
            textTransform: "uppercase",
            color: "#f7f7f7",
          },
        },
      }),
    []
  );

  return {
    theme,
    CssBaseline,
    GlobalStyles: (
      <GlobalStyles
        styles={{
          body: {
            margin: 0,
            padding: 0,
            backgroundColor: theme.palette.primary.main,
            minHeight: "100vh",
            display: "flex",
            flexDirection: "column",
          },
          html: {
            margin: 0,
            padding: 0,
            height: "100%",
            width: "100%",
          },
          "#root": {
            height: "100%",
            width: "100%",
            display: "flex",
            flexDirection: "column",
          },
        }}
      />
    ),
  };
};
